.background{
    width: 100%;
    height: 100vh;
    position: relative;
}

.desk-img{
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}

.background::after {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: white;
    opacity: 0.8; 
}

.hero {
    height: 100%;
    width: 100%;
}

.hero .overlay {
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: center;
}

.hero p {
    font-size: 23px;
    color: rgb(189, 189, 189);
    padding-bottom: 16px;
}

.hero h1 {
    font-size: 28px;
    color: white;
    padding-bottom: 28px;
}

.overlay .btn {
    margin: 1rem;
}

@media screen and (max-width:640px) {
    .hero p {
        font-size: 18px;
        padding-bottom: 10px;
    }
    .hero h1 {
        font-size: 20px;
        padding-bottom: 24px;
    }
    .btn {
        font-size: 14px;
    }
    .btn-light {
        font-size: 14px;
    }
}
